@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Black.ttf");
  font-weight: normal;
}
#poststuff .inside {
  margin: 0;
}

.postbox .inside {
  margin: 0;
  padding: 0;
}

/* Container for the form group */
.bsr-form-group {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  /* Bottom border for the group */
  border-bottom: 1px solid #ccc;
  /* Individual items within the form group */
}
@media (max-width: 768px) {
  .bsr-form-group {
    flex-direction: column;
    align-items: flex-start;
  }
}
.bsr-form-group .bsr-form-item {
  flex: 1 1 48%; /* Two columns, each taking up roughly half the space */
  padding: 10px;
  /* Border between items (except the last one) */
  /* Bottom border for each item */
  /* Styling for the select box */
}
@media (max-width: 768px) {
  .bsr-form-group .bsr-form-item {
    flex: 1 1 100%;
  }
}
.bsr-form-group .bsr-form-item:not(:last-child) {
  border-right: 1px solid #ccc;
}
@media (max-width: 768px) {
  .bsr-form-group .bsr-form-item:not(:last-child) {
    border-right: none;
  }
}
.bsr-form-group .bsr-form-item .bsr-form-select {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}